/*
 * action types
 */

export const WRITE = "WRITE";
export const GET_DATA = "GET_DATA";
export const READ_DATA = "READ_DATA";
/*
 * action creators
 */

export function writeText(payload) {
  return {
    type: WRITE,
    payload
  };
}

export function getData() {
  return { type: GET_DATA };
}

export function findOneBook(payload) {
  return {
    type: READ_DATA,
    payload
  };
}
