import React from "react";
import { connect } from "react-redux";

const mapStateToProps = (state) => {
  return {
    text: state.text
  };
};

const ConnectForm = ({ text }) => (
  <div>
    <p></p>
    The data from parent is:{text}
  </div>
);

const AlertForm = connect(mapStateToProps)(ConnectForm);

export default AlertForm;
