import { WRITE } from "../actions/actions";

const initialState = {
  text: "",
  remoteArticles: [],
  searchArticles: []
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case WRITE:
      return {
        ...state,
        text: action.payload
      };
    case "DATA_LOADED":
      return Object.assign({}, state, {
        remoteArticles: state.remoteArticles.concat(action.payload)
      });
    case "API_ERRORED":
      return Object.assign({}, state, {
        remoteArticles: state.remoteArticles.concat(action.payload)
      });
    case "DATA_READED":
      return Object.assign({}, state, {
        searchArticles: state.searchArticles.concat(action.payload)
      });
    case "API_READ_ERRORED":
      return Object.assign({}, state, {
        searchArticles: state.searchArticles.concat(action.payload)
      });
    default:
      return state;
  }
};

export default reducer;
