import { configureStore, getDefaultMiddleware } from "@reduxjs/toolkit";
import reducer from "../reducers/reducers";
import { forbiddenWordsMiddleware } from "../middleware/middleware";
import createSagaMiddleware from "redux-saga";
import rootSaga from "../sagas/index";

let sagaMiddleware = createSagaMiddleware();
const middleware = [
  ...getDefaultMiddleware({ thunk: false, serializableCheck: false }),
  forbiddenWordsMiddleware,
  sagaMiddleware
];

const storeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__;

const store = configureStore({
  reducer,
  storeEnhancers,
  middleware
});

sagaMiddleware.run(rootSaga); //tengo que ver c√≥mo llamar esto cuando hago el render del compoenente

export default store;
