import React from "react";
import { connect } from "react-redux";

class FindOne extends React.Component {
  constructor(props) {
    super(props);
  }

  getValue() {
    return this.props.books;
  }

  render() {
    return (
      <input
        class="form-control"
        type="text"
        placeholder={this.props.books}
        current={this.getValue()}
      />
    );
  }
}

function mapStateToProps(state) {
  return {
    books: state.text
  };
}

export default connect(mapStateToProps)(FindOne);
