const ROOT_URL = "https://mongo-go-test.herokuapp.com/";

export class ApiBooks {
  // created sigin api function
  static fetchBooks(action) {
    const url = `${ROOT_URL}api/books`;
    return fetch(url).then((response) => response.json());
  }

  // created get user api function
  static fetchOneBook(action) {
    console.log(action.payload);
    const url = `${ROOT_URL}api/books/${action.payload}`; //
    return fetch(url).then((response) => response.json());
  }
} //cierra todo

export default ApiBooks;
