import { all, takeEvery, call, put } from "redux-saga/effects";
import ApiBooks from "../api/api";

function* workerSaga1(action) {
  try {
    const payload = yield call(ApiBooks.fetchBooks, action);
    yield put({ type: "DATA_LOADED", payload });
  } catch (e) {
    yield put({ type: "API_ERRORED", payload: e });
  }
}

function* workerSaga2(action) {
  try {
    const payload = yield call(ApiBooks.fetchOneBook, action);
    yield put({
      type: "DATA_READED",
      payload
    });
  } catch (e) {
    yield put({ type: "API_READ_ERRORED", payload: e });
  }
}

export default function* rootSaga() {
  yield all([
    takeEvery("GET_DATA", workerSaga1),
    takeEvery("READ_DATA", workerSaga2)
  ]);
  //yield all([fork(workerSaga1), fork(workerSaga2)]);
}
