import React from "react";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import { connect } from "react-redux";
import { writeText } from "../actions/actions";

const Insert = ({ dispatch }) => {
  let input;

  return (
    <Form
      onSubmit={(e) => {
        e.preventDefault();
        if (!input.value.trim()) {
          return;
        }
        dispatch(writeText(input.value));
        input.value = "";
      }}
    >
      <Form.Label>
        Name:
        <input
          ref={(node) => (input = node)}
          class="form-control"
          type="text"
        />
      </Form.Label>
      <Button variant="primary" type="submit">
        Submit
      </Button>
    </Form>
  );
};

export default connect()(Insert);
